/* Global */

body {
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
}

div.link {
  cursor: pointer;
}

h2 {
  color: rgb(76, 0, 139);
}

h3 {
  color: rgb(109, 9, 172);
}

a:link {
  cursor: pointer;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Content */

div.content-container {
  width: 90%;
  margin: auto;
  overflow: auto;
}

div.content {
  float: left;
  width: 99%;
}

div.content img {
  width: 700px;
  height: auto;
}

div.external-link a {
  color: rgb(0, 121, 61);
}

div.external-link {
  padding: 8px;
  text-align: right;
}

/* Menu */

div.menu a {
  color: rgb(0, 62, 121);
}

div.menu {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  background-color: rgb(185, 220, 240);
  width: 95%;
  margin: auto;
}

div.menu h1 {
  color: rgb(1, 51, 80);
}

/* Footer */

div.footer {
  background-color: rgb(185, 220, 240);
  padding: 3px;
  top: 10px;
  padding-left: 15px;
  margin: auto;
  margin-top: 10px;
}

/* Togglable */

div.togglable {
  width: 150px;
  padding: 5px;
  margin: 8px 0px 5px 0px;
  background-color: rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  cursor: pointer;
}

div.togglable:hover {
  text-decoration: underline;
}

/* Buttons and Fields */

div.small-button {
  display: inline-block;
  margin-left: 5px;
  width: 50px;
  padding: 3px;
  background-color: rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}

button.submitButton {
  background-color: rgb(211, 241, 230);
  vertical-align: top;
  margin: 15px 10px 0px 0px;
  width: 100px;
  padding: 3px;
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}

input.nameField, textarea.commentField {
  left: 200px;
  margin: 2px 5px 4px 10px;
  width: 520px;
}

textArea.commentField {
  height: 100px;
}

form.commentForm {
  text-align: justify;
  max-width: 580px;
  display: grid;
  border: 2px solid rgb(139, 149, 158);
  border-radius: 4px;
  padding: 5px 5px 5px 20px;
}

/* Search */

img.search-img {
  height: 500px;
  width: auto;
}

/* APOD */

div.apodImg {
  text-align: center;
  min-height: 500px;
}

div.previous-apod {
  padding: 10px 0px 10px 50px;
}

div.apod-desc {
  text-align: justify;
  padding: 20px;
}

.apod-search-field {
  height: 29px;
  border-radius: 4px;
  background-color: rgb(228, 239, 245);
  font-size: 14px;
  padding: 4px;
  margin: 10px 10px 10px 0px;
  box-sizing: border-box;
  float: inline-start;
}

.apod-search-button {
  height: 25px;
  float: left;
  border-radius: 4px;
  background-color: rgb(196, 232, 252);
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  font-size: 14px;
  margin: 0px 10px 10px 0px;
}

div.field-box {
  text-align: justify;
  width: 230px;
  display: grid;
  border: 2px solid rgb(139, 149, 158);
  border-radius: 4px;
  padding: 5px 5px 5px 20px;
  overflow: hidden;
}

/* Search results */

div.result-dark {
  padding: 5px 10px 10px 10px;
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  color: rgb(5, 34, 49);
  background-color: rgb(228, 239, 245);
}

div.result-bright {
  padding: 5px 10px 10px 10px;
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  background-color: white;
}

div.img-fail {
  padding: 5px 10px 10px 10px;
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  background-color: rgb(228, 244, 245);
  width: 80%;
  border: 0;
  border-radius: 4px;
}

div.img-fail-box {
  width: 95%;
  margin-left: 50px;
}

/* Notification */

div.notif {
  background-color: pink;
  line-height: 30px;
  border-width: 2px;
  border-color: red;
  border-style: solid;
  border-radius: 3px;
  text-align: center;
  vertical-align: middle;
  padding: 10px;
}

div.empty-notif {
  background-color: white;
  height: 30px;
}

div.notif-div {
  float: left;
  margin-top: -97px;
  margin-left: 270px;
}

div.exclamation {
  font-size: xx-large;
  float: left;
  padding: 3px;
}